




import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import TheSettings from "@/components/settings/TheSettings.vue";
import { settingsModule, authModule } from "@/store/modules";

@Component({
  components: {
    TheSettings
  }
})
export default class SettingsView extends Mixins() {
  get allSettings() {
    return settingsModule.allSettings;
  }
  @Watch("allSettings", { deep: true, immediate: false })
  async syncSettings() {
    if (authModule.isLoggedIn) {
      await settingsModule.saveAllSettings();
    }
  }

  async mounted() {
    await settingsModule.fetchAllSettings();
    await settingsModule.getServiceUpdateAvailable();
  }
}
